@import "../public_func.sass";
$border-color: #999;

.trans {
	transition: all 0.4s; }

.fix {
	position: fixed; }

header {
	font-size: rem(16);
	position: fixed;
	box-sizing: border-box;
	width: 100%;
	padding: 0 rem(20);
	.toggle-menu {
		color: white;
		position: relative;
		width: rem(60);
		height: rem(60);
		border-radius: rem(10);
		float: right;
		border: none;
		background: none;
		margin-top: rem(20);
		font-size: rem(40);
		cursor: pointer;
		&::after {
				content: "";
				display: block;
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background: black;
				opacity: 0.2; } }
	strong {
		color: white;
		border-radius: rem(10);
		float: left;
		font-weight: 300;
		font-size: rem(18);
		line-height: rem(50);
		text-transform: uppercase;
		padding: 0 rem(10);
		background: rgba(255,0,29,.7);
		margin-top: rem(20); }
	.title {
		color: white;
		padding-top: rem(100);
		font-size: rem(60);
		line-height: inherit;
		text-transform: uppercase;
		padding-bottom: rem(50); }
	nav {
		transition: all 0.4s;
		position: absolute;
		width: 0;
		overflow-x: hidden;
		max-width: 800px;
		height: 100vh;
		right: 0;
		top: 0;
		box-sizing: border-box;
		background: #F8F8F8;
		float: right;
		font-weight: 500;
		.title-wrap {
			width: 100%;
			line-height: rem(24);
			border-top: rem(3) solid $border-color;
			border-bottom: rem(1) solid $border-color;
			h2 {
				float: left; }
			span {
				float: left;
				display: block;
				font-size: rem(12);
				padding-top: rem(25);
				margin-left: rem(10);
				color: rgba(255, 0, 29, .7);
				&::before {
					content: "( "; }
				&::after {
					content: " )"; } } }
		&.on {
			width: 100%; }
		h2 {
			padding-top: rem(10);
			padding-left: rem(10);
			font-size: rem(24);
			padding-bottom: rem(10);
			&:nth-child(2) {
				padding-top: rem(60); } }
		ul {
			line-height: rem(50);
			padding-bottom: rem(80);
			button, a {
				cursor: pointer;
				display: block;
				width: 100%;
				color: black;
				text-decoration: none;
				text-align: left;
				line-height: inherit;
				background: none;
				border: 0;
				position: relative;
				&::after {
					content: "";
					transition: all 0.4s;
					display: block;
					background: #E9EDF1;
					opacity: 0.3;
					position: absolute;
					top: 0;
					left: 0;
					width: 0;
					height: rem(50); }
				&:hover, &:focus {
					&::after {
						width: 100%;
						background: red; } } }

			li {
				position: relative;
				border-top: rem(1) dashed $border-color;
				padding: rem(0) 0 rem(0) rem(50);
				line-height: inherit;
				&.on button {
					color: black;
					&::after {
						background: blue;
						width: 100%; } }
				button {
					font-weight: 500; }
				.description {
					position: absolute;
					left: rem(100);
					color: rgba(255, 0, 29, .7); } }
			li:first-child {
				border: 0; }
			li::before {
				content: "⇥";
				position: absolute;
				left: rem(25); }
			i {
				font-size: rem(40); } }
		.toggle-menu {
			position: absolute;
			bottom: 0;
			width: 100%;
			border-radius: 0;
			color: black; }
		.scroll {
			min-height: 100vh;
			position: relative; } } }

footer {
	padding: rem(100) 0;
	color: white;
	background: #556655;
	ul {
		li {
			margin-top: rem(10);
			a {
				color: #48a0ef;
				font-size: rem(50);
				&:hover {
					opacity: .5; } } } } }
